'use client';

import { useState, useEffect } from 'react';
// ÁßªÈô§Áõ¥Êé•ÂØºÂÖ•ÊúçÂä°Á´ØÂáΩÊï∞ÔºåÊîπ‰∏∫ÈÄöËøáAPIË∑ØÁî±Ëé∑ÂèñÊï∞ÊçÆ
import { getReceiptfgDataBatch } from './api/receiptfg/fetch-batch';
import { getReceiptfcDataBatch } from './api/receiptfc/fetch-batch';
// import Component from "./disposal/auction/page";
import ProgressiveTableWithPagination from '../components/progressive-table-with-pagination';
import dayjs from 'dayjs';

interface TableData {
  id: number;
  saleMemberId: string | null;
  saleMemberName: string | null;
  taxInclu: string | number | null;
  unitpriceIncluTax: string | number | null;
  wasteTypeName: string | null;
  weight: string | number | null;
  orderTime: Date | string | null;
  carNumber: string | null;
  carBrand: string | null;
  hasImage: boolean;
  progressiveUrls?: any;
  thumbnailSource?: 'database' | 'cdn';
}

export default function Page() {
  // Áî®‰∫éÁªüËÆ°ÁöÑÊï∞ÊçÆÔºà‰ªÖÁî®‰∫éËÆ°ÁÆóÊÄªÊï∞Á≠âÁªüËÆ°‰ø°ÊÅØÔºâ
  const [fcTotalData, setFcTotalData] = useState<TableData[]>([]);
  const [fgTotalData, setFgTotalData] = useState<TableData[]>([]);
  const [fcStatsLoading, setFcStatsLoading] = useState(true);
  const [fgStatsLoading, setFgStatsLoading] = useState(true);

  useEffect(() => {
    // Êä•Â∫üËΩ¶ÁªüËÆ°Êï∞ÊçÆÂä†ËΩΩÔºàÈÄöËøáAPIË∑ØÁî±Ôºâ
    const fetchFcStatsData = async () => {
      try {
        setFcStatsLoading(true);
        const response = await fetch('/api/receiptfc/fetch');
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        const result = await response.json();
        setFcTotalData(result);
      } catch (error) {
        console.error('Ëé∑ÂèñFCÁªüËÆ°Êï∞ÊçÆÂ§±Ë¥•:', error);
        setFcTotalData([]);
      } finally {
        setFcStatsLoading(false);
      }
    };

    // Â∫üÈí¢ÁªüËÆ°Êï∞ÊçÆÂä†ËΩΩÔºàÈÄöËøáAPIË∑ØÁî±Ôºâ
    const fetchFgStatsData = async () => {
      try {
        setFgStatsLoading(true);
        const response = await fetch('/api/receiptfg');
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        const result = await response.json();
        setFgTotalData(result);
      } catch (error) {
        console.error('Ëé∑ÂèñFGÁªüËÆ°Êï∞ÊçÆÂ§±Ë¥•:', error);
        setFgTotalData([]);
      } finally {
        setFgStatsLoading(false);
      }
    };

    // Âπ∂Ë°åÂêØÂä®ÁªüËÆ°Êï∞ÊçÆÂä†ËΩΩ
    fetchFcStatsData();
    fetchFgStatsData();
  }, []);

  // ËÆ°ÁÆóÁªüËÆ°Êï∞ÊçÆ
  const getTotalAmount = () => {
    const fcAmount = fcStatsLoading ? 0 : fcTotalData.reduce((sum, item) => sum + Number(item.taxInclu || 0), 0);
    const fgAmount = fgStatsLoading ? 0 : fgTotalData.reduce((sum, item) => sum + Number(item.taxInclu || 0), 0);
    return fcAmount + fgAmount;
  };

  return (
    <main className="p-6 bg-gray-50 dark:bg-gray-900 min-h-screen">
      <div className="max-w-7xl mx-auto">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 dark:text-white">ÂÜçÁîüËµÑÊ∫ê‰∫§ÊòìÊï∞ÊçÆË°®</h1>
          <p className="mt-2 text-sm text-gray-700 dark:text-gray-300">
            Êü•ÁúãÊØèÂ§©ÂÜçÁîüËµÑÊ∫ê‰∫§ÊòìËÆ∞ÂΩïÂíåËØ¶ÁªÜ‰ø°ÊÅØ
          </p>
        </div>
        
        {/* FC Êï∞ÊçÆË°® - ‰ΩøÁî®Â∏¶ÂàÜÈ°µÁöÑÊ∏êËøõÂºèÂä†ËΩΩÁªÑ‰ª∂ */}
        <ProgressiveTableWithPagination
          title="Êä•Â∫üËΩ¶ Êï∞ÊçÆ"
          subtitle="ÊúÄÊñ∞Êó•ÊúüÁöÑÂÖ®Â§©‰∫§ÊòìËÆ∞ÂΩï"
          bgColor="bg-white dark:bg-gray-800"
          fetchBatchData={getReceiptfcDataBatch}
          itemsPerPage={10}
        />
        
        {/* FG Êï∞ÊçÆË°® - ‰ΩøÁî®Â∏¶ÂàÜÈ°µÁöÑÊ∏êËøõÂºèÂä†ËΩΩÁªÑ‰ª∂ */}
        <ProgressiveTableWithPagination
          title="Â∫üÈí¢ÈìÅ Êï∞ÊçÆ"
          subtitle="ÊúÄÊñ∞Êó•ÊúüÁöÑÂÖ®Â§©‰∫§ÊòìËÆ∞ÂΩï"
          bgColor="bg-blue-50 dark:bg-gray-800"
          fetchBatchData={getReceiptfgDataBatch}
          itemsPerPage={10}
        />
        
        {/* ÁªüËÆ°‰ø°ÊÅØ */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mt-8">
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">
              {fcStatsLoading ? (
                <div className="h-8 bg-gray-200 dark:bg-gray-700 rounded animate-pulse"></div>
              ) : (
                fcTotalData.length
              )}
            </div>
            <div className="text-sm text-gray-600 dark:text-gray-400">Êä•Â∫üËΩ¶ ‰∫§ÊòìËÆ∞ÂΩï</div>
          </div>
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <div className="text-2xl font-bold text-green-600 dark:text-green-400">
              {fgStatsLoading ? (
                <div className="h-8 bg-gray-200 dark:bg-gray-700 rounded animate-pulse"></div>
              ) : (
                fgTotalData.length
              )}
            </div>
            <div className="text-sm text-gray-600 dark:text-gray-400">Â∫üÈí¢ÈìÅ ‰∫§ÊòìËÆ∞ÂΩï</div>
          </div>
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <div className="text-2xl font-bold text-purple-600 dark:text-purple-400">
              {fcStatsLoading || fgStatsLoading ? (
                <div className="h-8 bg-gray-200 dark:bg-gray-700 rounded animate-pulse"></div>
              ) : (
                fcTotalData.length + fgTotalData.length
              )}
            </div>
            <div className="text-sm text-gray-600 dark:text-gray-400">ÊÄª‰∫§ÊòìËÆ∞ÂΩï</div>
          </div>
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
            <div className="text-2xl font-bold text-orange-600 dark:text-orange-400">
              {fcStatsLoading || fgStatsLoading ? (
                <div className="h-8 bg-gray-200 dark:bg-gray-700 rounded animate-pulse"></div>
              ) : (
                `¬•${getTotalAmount().toLocaleString()}`
              )}
            </div>
            <div className="text-sm text-gray-600 dark:text-gray-400">ÊÄª‰∫§ÊòìÈáëÈ¢ù</div>
          </div>
        </div>
      </div>
    </main>
  );
}