'use client';

import { useState, useEffect, useRef } from 'react';
import { createPortal } from 'react-dom';
import { XMarkIcon } from '@heroicons/react/24/outline';

interface InstantThumbnailProps {
  progressiveUrls: {
    tiny: string;
    small: string;
    medium: string;
    original: string;
  };
  alt: string;
  className?: string;
  thumbnailSource?: 'database' | 'cdn';
}

export default function InstantThumbnail({ 
  progressiveUrls, 
  alt, 
  className = "",
  thumbnailSource = 'cdn'
}: InstantThumbnailProps) {
  const [currentSrc, setCurrentSrc] = useState<string>('');
  const [showModal, setShowModal] = useState(false);
  const [imageError, setImageError] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  
  // Êñ∞Â¢ûÊ®°ÊÄÅÊ°ÜÁõ∏ÂÖ≥Áä∂ÊÄÅ
  const [modalImageSrc, setModalImageSrc] = useState<string>('');
  const [isLoadingOriginal, setIsLoadingOriginal] = useState(false);
  const [originalLoaded, setOriginalLoaded] = useState(false);
  const [mounted, setMounted] = useState(false);
  
  const imgRef = useRef<HTMLImageElement>(null);

  // Á°Æ‰øùÂè™Âú®ÂÆ¢Êà∑Á´ØÊ∏≤ÊüìPortal
  useEffect(() => {
    setMounted(true);
  }, []);

  // Ë∞ÉËØï‰ø°ÊÅØ
  useEffect(() => {
    console.log('InstantThumbnail progressiveUrls:', progressiveUrls);
    console.log('InstantThumbnail showModal:', showModal);
  }, [progressiveUrls, showModal]);

  // ‰ºòÂåñÁöÑÊ∏êËøõÂºèÂä†ËΩΩÁ≠ñÁï•
  useEffect(() => {
    if (!progressiveUrls.tiny) return;

    let isCancelled = false;
    
    const loadSequence = async () => {
      try {
        // Â¶ÇÊûúÊòØÊï∞ÊçÆÂ∫ìÁº©Áï•ÂõæÔºàbase64ÔºâÔºåÁ´ãÂç≥ÊòæÁ§∫
        if (thumbnailSource === 'database' && progressiveUrls.tiny.startsWith('data:')) {
          if (!isCancelled) {
            setCurrentSrc(progressiveUrls.tiny);
            setIsLoading(false);
          }
          
          // Âª∂ËøüÂä†ËΩΩÊõ¥È´òË¥®ÈáèÁâàÊú¨
          setTimeout(async () => {
            if (!isCancelled && progressiveUrls.small && progressiveUrls.small !== progressiveUrls.tiny) {
              setCurrentSrc(progressiveUrls.small);
            }
          }, 200);
          
          // ËßÜÂè£ÂÜÖÊâçÂä†ËΩΩ‰∏≠Á≠âË¥®ÈáèÂõæÁâá
          setTimeout(() => {
            if (!isCancelled && progressiveUrls.medium && imgRef.current) {
              const observer = new IntersectionObserver(
                (entries) => {
                  if (entries[0].isIntersecting) {
                    setCurrentSrc(progressiveUrls.medium);
                    observer.disconnect();
                  }
                },
                { threshold: 0.1 }
              );
              observer.observe(imgRef.current);
            }
          }, 500);
          
        } else {
          // CDNÁº©Áï•ÂõæÁöÑÊ∏êËøõÂä†ËΩΩ
          setCurrentSrc(progressiveUrls.tiny);
          setIsLoading(false);
          
          // È¢ÑÂä†ËΩΩÂêéÁª≠ÁâàÊú¨
          if (progressiveUrls.small && progressiveUrls.small !== progressiveUrls.tiny) {
            const img = new Image();
            img.onload = () => {
              if (!isCancelled) {
                setCurrentSrc(progressiveUrls.small);
              }
            };
            img.src = progressiveUrls.small;
          }
        }
        
      } catch (error) {
        console.warn('ÂõæÁâáÂä†ËΩΩÂ§±Ë¥•:', error);
        if (!isCancelled) {
          setImageError(true);
          setIsLoading(false);
        }
      }
    };

    loadSequence();
    
    return () => {
      isCancelled = true;
    };
  }, [progressiveUrls, thumbnailSource]);

  // ÈîÆÁõò‰∫ã‰ª∂ÁõëÂê¨
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape' && showModal) {
        closeModal();
      }
    };

    if (showModal) {
      document.addEventListener('keydown', handleKeyDown);
      document.body.style.overflow = 'hidden';
    }

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
      document.body.style.overflow = 'unset';
    };
  }, [showModal]);

  // Ê®°ÊÄÅÊ°ÜÂéüÂßãÂõæÁâáÂä†ËΩΩÈÄªËæë
  useEffect(() => {
    if (!showModal) {
      setIsLoadingOriginal(false);
      setOriginalLoaded(false);
      setModalImageSrc(''); // Ê∏ÖÁ©∫Ê®°ÊÄÅÊ°ÜÂõæÁâáÊ∫ê
      return;
    }

    // Ê£ÄÊü•progressiveUrlsÊòØÂê¶ÊúâÊïà
    if (!progressiveUrls || !progressiveUrls.original) {
      console.warn('progressiveUrlsÊó†ÊïàÔºåÂÖ≥Èó≠Ê®°ÊÄÅÊ°Ü');
      setShowModal(false);
      return;
    }

    // Ê®°ÊÄÅÊ°ÜÊâìÂºÄÊó∂ÔºåÂÖàÊòæÁ§∫‰∏≠Á≠âË¥®ÈáèÂõæÁâáÔºåÂêåÊó∂ÂºÄÂßãÂä†ËΩΩÂéüÂßãÂõæÁâá
    const bestAvailableImage = progressiveUrls.medium || progressiveUrls.small || currentSrc;
    
    console.log('Ê®°ÊÄÅÊ°ÜÊâìÂºÄÔºåÊúÄ‰Ω≥ÂèØÁî®ÂõæÁâá:', bestAvailableImage);
    console.log('ÂéüÂßãÂõæÁâáURL:', progressiveUrls.original);
    
    // Á°Æ‰øùÊúâÊúâÊïàÁöÑÂõæÁâáURLÊâçËÆæÁΩÆ
    if (bestAvailableImage && bestAvailableImage.trim() !== '') {
      setModalImageSrc(bestAvailableImage);
      setIsLoadingOriginal(true);
      setOriginalLoaded(false);

      // Âú®ÂêéÂè∞Âä†ËΩΩÂéüÂßãÂõæÁâá
      const img = new Image();
      img.onload = () => {
        console.log('ÂéüÂßãÂõæÁâáÂä†ËΩΩÊàêÂäü');
        setIsLoadingOriginal(false);
        // ÂÖàÂàáÊç¢Âà∞ÂéüÂßãÂõæÁâáÊ∫êÔºå‰ΩÜ‰øùÊåÅoriginalLoaded‰∏∫false‰ª•Ëß¶ÂèëËøáÊ∏°Âä®Áîª
        setTimeout(() => {
          setModalImageSrc(progressiveUrls.original);
          // Áü≠ÊöÇÂª∂ËøüÂêéËÆæÁΩÆ‰∏∫Â∑≤Âä†ËΩΩÔºåËß¶Âèë‰ªéÊ®°Á≥äÂà∞Ê∏ÖÊô∞ÁöÑËøáÊ∏°
          setTimeout(() => {
            setOriginalLoaded(true);
          }, 100);
        }, 200);
      };
      img.onerror = () => {
        console.error('ÂéüÂßãÂõæÁâáÂä†ËΩΩÂ§±Ë¥•:', progressiveUrls.original);
        setIsLoadingOriginal(false);
        console.warn('ÂéüÂßãÂõæÁâáÂä†ËΩΩÂ§±Ë¥•Ôºå‰øùÊåÅ‰ΩøÁî®‰∏≠Á≠âË¥®ÈáèÂõæÁâá');
      };
      img.src = progressiveUrls.original;
    } else {
      // Â¶ÇÊûúÊ≤°ÊúâÂèØÁî®ÁöÑÂõæÁâáÔºåÂÖ≥Èó≠Ê®°ÊÄÅÊ°Ü
      setShowModal(false);
      console.warn('Ê≤°ÊúâÂèØÁî®ÁöÑÂõæÁâáURL');
    }

  }, [showModal, progressiveUrls, currentSrc]);

  if (imageError || !progressiveUrls.tiny) {
    return (
      <div className={`${className} bg-gray-100 dark:bg-gray-700 rounded-lg flex items-center justify-center`}>
        <span className="text-xs text-gray-400">Êó†ÂõæÁâá</span>
      </div>
    );
  }

  const handleThumbnailClick = () => {
    console.log('Áº©Áï•ÂõæË¢´ÁÇπÂáªÔºåprogressiveUrls:', progressiveUrls);
    console.log('Â∞ÜshowModalËÆæÁΩÆ‰∏∫true');
    setShowModal(true);
  };

  const closeModal = () => {
    setShowModal(false);
  };

  const handleBackdropClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      closeModal();
    }
  };

  // Ê®°ÊÄÅÊ°ÜÁªÑ‰ª∂
  const Modal = () => (
    <div 
      className="fixed inset-0 bg-black bg-opacity-80 flex items-center justify-center p-4"
      style={{ 
        zIndex: 999999,
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        width: '100vw',
        height: '100vh'
      }}
      onClick={handleBackdropClick}
    >
      {/* ÂÖ≥Èó≠ÊåâÈíÆ - Âè™Âú®ÂõæÁâáÂä†ËΩΩÂÆåÊàêÂêéÊòæÁ§∫ */}
      {originalLoaded && modalImageSrc === progressiveUrls.original && (
        <button
          onClick={closeModal}
          className="absolute top-4 right-4 bg-black bg-opacity-60 text-white rounded-full p-3 hover:bg-opacity-80 transition-colors shadow-lg"
          style={{ zIndex: 999999 }}
          title="ÂÖ≥Èó≠ (ESC)"
        >
          <XMarkIcon className="h-6 w-6" />
        </button>
      )}
      
      {/* Âä†ËΩΩÊåáÁ§∫Âô® - ÂõæÁâáÊ≠£‰∏äÊñπÔºåÂè™ÊúâËΩ¨ÂúàÂúà */}
      {isLoadingOriginal && (
        <div 
          className="absolute flex items-center justify-center"
          style={{ 
            zIndex: 999999,
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, calc(-50% - 45vh))', // ÊîæÂú®ÂõæÁâáÊ≠£‰∏äÊñπ
            marginBottom: '1rem'
          }}
        >
          <div className="animate-spin rounded-full h-8 w-8 border-3 border-white border-t-transparent opacity-90"></div>
        </div>
      )}
      
      {/* ÂõæÁâáÂÆπÂô® - Âº∫Âà∂Â§ßÂ∞∫ÂØ∏ÊòæÁ§∫ */}
      {modalImageSrc && modalImageSrc.trim() !== '' ? (
        <img 
          src={modalImageSrc === progressiveUrls.original ? progressiveUrls.original : modalImageSrc} 
          alt={alt}
          className="object-contain rounded-lg shadow-2xl"
          style={{
            width: '80vw',
            height: '80vh',
            filter: originalLoaded && modalImageSrc === progressiveUrls.original 
              ? 'none' 
              : isLoadingOriginal 
                ? 'blur(4px) brightness(0.9) contrast(1.1)' 
                : 'blur(3px) brightness(0.95)',
            transform: originalLoaded && modalImageSrc === progressiveUrls.original 
              ? 'scale(1)' 
              : 'scale(1.01)',
            opacity: originalLoaded && modalImageSrc === progressiveUrls.original ? 1 : 0.9,
            transition: 'all 1500ms ease-out'
          }}
          onClick={(e) => e.stopPropagation()}
        />
      ) : (
        <div 
          className="bg-gray-100 dark:bg-gray-700 rounded-lg flex items-center justify-center"
          style={{ 
            width: '80vw', 
            height: '80vh'
          }}
        >
          <div className="text-center">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-400 mx-auto mb-3"></div>
            <span className="text-gray-500 dark:text-gray-400">ÂõæÁâáÂä†ËΩΩ‰∏≠...</span>
          </div>
        </div>
      )}
    </div>
  );

  return (
    <>
      {/* Áû¨Èó¥ÊòæÁ§∫ÁöÑÁº©Áï•Âõæ */}
      <div className="relative group">
        {isLoading && (
          <div className={`${className} bg-gray-200 dark:bg-gray-600 animate-pulse rounded-lg`}>
          </div>
        )}
        {currentSrc && (
          <img 
            ref={imgRef}
            src={currentSrc} 
            alt={alt}
            className={`${className} ${isLoading ? 'opacity-0' : 'opacity-100'} transition-all duration-200 cursor-pointer hover:opacity-90 hover:scale-105 shadow-sm hover:shadow-md rounded-lg object-cover`}
            onError={() => setImageError(true)}
            onLoad={() => setIsLoading(false)}
            onClick={handleThumbnailClick}
            title="ÁÇπÂáªÊü•ÁúãÂ§ßÂõæ"
            loading="eager" // Á´ãÂç≥Âä†ËΩΩ
          />
        )}
      </div>

      {/* ‰ΩøÁî®PortalÊ∏≤ÊüìÂ§ßÂõæÊ®°ÊÄÅÊ°ÜÂà∞body */}
      {showModal && mounted && createPortal(<Modal />, document.body)}
    </>
  );
} 